<template>
  <div class="reportinfo">
    <el-row :gutter="20" class="report-wrap">
      <el-col :span="14">
        <fieldset>
          <legend></legend>
          <div style="padding:15px;">
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==1"
            >
              <el-form-item label="" prop="projectName">
                <el-input v-model="reportBasic.projectName" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="ownName">
                <el-input v-model="reportBasic.ownName" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="arealeaderNames">
                <el-input v-model="reportBasic.arealeaderNames" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="streets">
                <el-input v-model="reportBasic.streets" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="category">
                <el-input v-model="reportBasic.category" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="address">
                <el-input readonly v-model="reportBasic.address"></el-input>
              </el-form-item>
              <el-form-item label="" prop="submitres">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1}"
                  readonly
                  v-model="reportBasic.submitres"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="remark">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1}"
                  readonly
                  v-model="reportBasic.remark"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="aimplan">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1}"
                  readonly
                  v-model="reportBasic.aimplan"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==2"
            >
              <el-form-item label="" prop="code">
                <el-input readonly v-model="reportBasic.code"></el-input>
              </el-form-item>
              <el-form-item label="" prop="starttime">
                <el-date-picker
                  v-model="reportBasic.starttime"
                  readonly
                  type="date"
                  class="el-input-datebox"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-form-item>
              <el-form-item label="" prop="tasksource">
                <el-input readonly v-model="reportBasic.tasksource"></el-input>
              </el-form-item>
              <el-form-item label="" prop="leadername">
                <el-input readonly v-model="reportBasic.leadername"></el-input>
              </el-form-item>
              <el-form-item label="" prop="jobrequire">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 5}"
                  readonly
                  v-model="reportBasic.jobrequire"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  &&  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="120px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==3||itemShow==4"
            >
              <el-form-item label="" prop="yearname">
                <el-input v-model="reportBasic.yearname" readonly></el-input>
              </el-form-item>
              <el-form-item :label="itemShow==3?'':''" prop="bigname">
                <el-input v-model="reportBasic.bigname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="minname">
                <el-input v-model="reportBasic.minname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="partname">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1 }"
                  readonly
                  v-model="reportBasic.partname"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="superdept">
                <el-input v-model="reportBasic.superdept" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaders" class="qt">
                <el-input v-model="reportBasic.leaders" readonly></el-input>
              </el-form-item>
              <el-form-item :label="itemShow==3?'':'()'" prop="workname">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1 }"
                  readonly
                  v-model="reportBasic.workname"
                ></el-input>
              </el-form-item>
              <el-form-item :label="itemShow==3?'':''" prop="nodeplan">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 1 }"
                  readonly
                  v-model="reportBasic.nodeplan"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  &&  -->
            <!-- <el-form :model="reportBasic" size="small" label-width="110px" ref="reportBasic" :append-to-body="true" v-if="itemShow==5||itemShow== 6" >
              <el-form-item label="" prop="year">
                <el-input v-model="reportBasic.year" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="superdept">
                <el-input v-model="reportBasic.superdept" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="arealeadnames" class="qt">
                <el-input v-model="reportBasic.arealeadnames" readonly></el-input>
              </el-form-item>
              <el-form-item :label="itemShow==5?'':''" prop="nodeplan">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 4}"
                  readonly
                  v-model="reportBasic.nodeplan"
                ></el-input>
              </el-form-item>
              <el-form-item :label="itemShow==5?'':''" prop="workname">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 4}"
                  readonly
                  v-model="reportBasic.workname"
                ></el-input>
              </el-form-item>
            </el-form> -->
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==7"
            >
              <el-form-item label="" prop="yearname">
                <el-input v-model="reportBasic.yearname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="workname">
                <el-input v-model="reportBasic.workname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="nodeplan">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 5, maxRows:10 }"
                  readonly
                  v-model="reportBasic.nodeplan"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="secondwork">
                <el-input readonly v-model="reportBasic.secondwork"></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaders" class="qt">
                <el-input v-model="reportBasic.leaders" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==8"
            >
              <el-form-item label="" prop="yearname">
                <el-input v-model="reportBasic.yearname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaders" class="qt">
                <el-input v-model="reportBasic.leaders" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="nodeplan">
                <el-input v-model="reportBasic.nodeplan" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="workname">
                <el-input readonly v-model="reportBasic.workname"></el-input>
              </el-form-item>
              <el-form-item label="" prop="timenode">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 3, maxRow: 10}"
                  readonly
                  v-model="reportBasic.timenode"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==9"
            >
              <el-form-item label="" prop="yearname">
                <el-input v-model="reportBasic.yearname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaders" class="qt">
                <el-input v-model="reportBasic.leaders" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="projectname">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 3}"
                  readonly
                  v-model="reportBasic.projectname"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="timenode">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 3}"
                  readonly
                  v-model="reportBasic.timenode"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" readonly type="textarea" :autosize="{ minRows: 1, maxRows:3}" ></el-input>
              </el-form-item>
            </el-form>
            <!--  -->
            <el-form
              :model="reportBasic"
              size="small"
              label-width="110px"
              ref="reportBasic"
              :append-to-body="true"
              v-if="itemShow==10"
            >
              <el-form-item label="" prop="yearname">
                <el-input v-model="reportBasic.yearname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="bigname">
                <el-input v-model="reportBasic.bigname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="minname">
                <el-input v-model="reportBasic.minname" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="workname">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 4}"
                  readonly
                  v-model="reportBasic.workname"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaders" class="qt">
                <el-input v-model="reportBasic.leaders" readonly></el-input>
              </el-form-item>
              <el-form-item label="" prop="leaderDeptName">
                <el-input v-model="reportBasic.leaderDeptName" type="textarea" :autosize="{ minRows: 1, maxRows:3}" readonly></el-input>
              </el-form-item>
            </el-form>
          </div>
        </fieldset>
      </el-col>
      <el-col :span="10">
        <fieldset>
          <legend></legend>
          <div style="padding:15px;">
            <el-form
              :model="reportData"
              size="small"
              label-width="125px"
              ref="reportData"
              :append-to-body="true"
            >
              <el-form-item label="" prop="laststate">
                <el-input type="textarea" readonly :autosize="{ minRows: 3, maxRows:5}" v-model="laststate"></el-input>
              </el-form-item>
              <el-form-item
                label=""
                prop="question"
                :rules="[{ required: true, message: '', trigger:'blur'}]"
              >
                <el-input type="textarea" :autosize="{ minRows: 3}" v-model="reportData.question"></el-input>
              </el-form-item>
              <el-form-item
                label=""
                prop="wenti"
                :rules="[{ required: true, message: '', trigger:'blur'}]"
              >
                <el-input type="textarea" :autosize="{ minRows: 3}" v-model="reportData.wenti"></el-input>
              </el-form-item>
              <el-form-item
                label=""
                prop="measure"
                :rules="[{ required: true, message: '', trigger:'blur'}]"
              >
                <el-input type="textarea" :autosize="{ minRows: 3}" v-model="reportData.measure"></el-input>
              </el-form-item>
              <el-form-item
                label=""
                prop="remark"
                :rules="[{ required: true, message: '', trigger:'blur'}]"
              >
                <el-radio-group v-model="reportData.remark" @change="selectChange">
                  <el-radio :label="0"></el-radio>
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="2"></el-radio>
                </el-radio-group>
              </el-form-item>
              <div class="upbox">
                <el-upload
                  class="upload-demo"
                  :action="upfile"
                  :on-remove="handleRemove"
                  :on-success="handleSuccess"
                  :before-remove="beforeRemove"
                  :on-preview="handlePreview"
                  :file-list="fileList"
                  ref="upload"
                >
                  <el-button size="small" type="primary"></el-button>
                </el-upload>
              </div>
            </el-form>
          </div>
        </fieldset>
      </el-col>
    </el-row>
    <div class="export-btn">
      <el-button size="small" :disabled="isExport" @click="exportData" type="primary"></el-button>
    </div>
  </div>
</template>
 
<script>
import { defaturl } from "../api/apiUrl";
import { upfile, imgIp } from "../api/imgUrl";
export default {
  name: "report",
  props: [
    "reportBasic",
    "reportVisible",
    "reportData",
    "fileArr",
    "fileList",
    "upfile",
    "laststate",
    "itemShow",
    "isExport",
    "workid"
  ],
  components: {},
  data() {
    return {
      loginid: JSON.parse(this.$store.getters.userdata).id,
      defaturl:"",
      imgIp:""
    };
  },
  created() {
    this.defaturl = defaturl;
    this.imgIp = imgIp;
  },
  watch: {
    reportVisible: function(newValue, oldValue) {
      this.$emit("update:reportVisible", newValue);
    },
    reportData: function(newValue, oldValue) {
      this.$emit("update:reportData", newValue);
    },
    fileArr: function(newValue, oldValue) {
      this.$emit("update:fileArr", newValue);
    },
    fileList: function(newValue, oldValue) {
      this.$emit("update:fileList", newValue);
    },
    isExport: function(newValue, oldValue) {
      this.$emit("update:isExport", newValue);
    }
  },
  methods: {
    // 
    exportData() {
      if(this.itemShow==7){
        window.open(this.defaturl + "/tripleWorkProgress/tripleWorkReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==8){
        window.open(this.defaturl + "/tripleAreaProgress/tripleAreaReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==9){
        window.open(this.defaturl + "/yy/importSituationProgressExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==3){
        window.open(this.defaturl + "/report/threeReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==4){
        window.open(this.defaturl + "/report/indicateReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==10){
        window.open(this.defaturl + "/report/pointReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype=12");
      }else if(this.itemShow==1){
        window.open(this.defaturl + "/report/projectReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }else if(this.itemShow==2){
        window.open(this.defaturl + "/report/checkReportExport?userid="+this.loginid+"&termid="+this.workid+"&modeltype="+this.itemShow);
      }
    },
    handlePreview(file,fileList){
      console.log(file)
      console.log(this.imgIp)
      var urls = this.imgIp + file.name;
      window.open(urls)
    },
    selectChange() {
      this.$forceUpdate();
    },
    //  
    handleRemove(file, fileList) {
      var name = file.name;
      this.fileArr.splice(this.removeArr(this.fileArr, name), 1);
    },
    //  
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    //  
    handleSuccess(file, fileList) {
      let name = file.obj;
      this.fileArr.push(name);
    }
  }
};
</script>

<style lang="scss" scope>
.export-btn {
  padding: 10px 36px 0;
  text-align: right;
}
.el-dialog__body {
  padding-bottom: 10px;
}
</style>



